:root {
    --swankyGray: #404050;
    --darkerSwankyGray: #303040;
    --ligtherSwankyGreen: #98c379;
    height:100%;
}

#root {
    height: 100%;
    width: 100%;
    margin: 0;

}

html,body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.TopBar {
    position: fixed;
    top: 0;
    left: 0;
    margin: auto ;
    padding: 0;
    background-color: var(--darkerSwankyGray);
    color: white;
    text-align: center;
    width:100%;
    height:6%;
    z-index: 1;
}

.Name {
    display: inline-block;
    position: absolute;
    top: 0;
    top: 35%;
    left: 0;
    font-family: "Arial";
    font-size: 100%;
    text-align: center;
    width:20%;
    height: 100%
}

.MainMenu {
    position: relative;
    margin: auto;
    padding: 0;
    height:100%;
}

@-webkit-keyframes Fade {
    0%, 100% {
        -webkit-box-shadow: 0px 12px 10px -10px ;
                box-shadow: 0px 12px 10px -10px ;
    }
    50% {
        -webkit-box-shadow: 0;
                box-shadow: 0;
    }
}

@keyframes Fade {
    0%, 100% {
        -webkit-box-shadow: 0px 12px 10px -10px ;
                box-shadow: 0px 12px 10px -10px ;
    }
    50% {
        -webkit-box-shadow: 0;
                box-shadow: 0;
    }
}

.ButtonThing:hover {
    -webkit-animation: lightFade;
            animation: lightFade;
}



