:root {
    --swankyGray: #404050;
    --darkerSwankyGray: #303040;
    --ligtherSwankyGreen: #98c379;
}
.Home {
  text-align: center;
}


.Content {
    top: 0;
    position: relative;
    overflow: scroll;
    text-align: center;
    color: white;
    font-family: Arial;
    overflow: scroll;
    width:100%;
    height:100%;
    background-color: var(--swankyGray);
}

.MyText {
    position: relative;
    text-align: center;
    margin-top:2%;
    margin-left:auto;
    margin-right:auto;
    margin-bottom:5%;
    width: 100%;
    white-space: pre-wrap;
    color: white; 
    background-color: var(--swankyGray);

}




