.Typist {text-align: center;}

.Typist .Cursor {
  display: inline-block;
  text-align: center}
  .Typist .Cursor--blinking {
    opacity: 1;
    text-align: center;
    -webkit-animation: blink 1s linear infinite;
            animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }



